import React, { Component } from 'react'
import QuestionButton from './utils/QuestionButton.js'
import questionJson from './questionsWWW1.json'
import Ladder from './Ladder'
import EndGameScreen from './EndGameScreen'
import styled from 'styled-components'

// colors
const green = 'green'
const orange = '#FFA500'
const white = '#3848CB'

const Wrapper = styled.div`
    height: 100vh;
    width: 100vh;
    left:0px;
    top:0px;
    border: 0;
    padding: 0;
    margin: -10px;
    margin-top:-16px;
    background-image: linear-gradient(to bottom right, #00008b, black);
`

const Answers = styled.div`
    position:absolute;
    top:630px;
    left:150px;
`

const QuestionWrapper = styled.div`
    position: absolute;
    top:550px;
    left:60px;
    background-color:#3848CB;
    height:50px;
    width:600px;
    color:white;
    padding:10px;
    border-style: solid;
    border-width: 2px;
    border-color: #C6D2FA;
    font-family: Arial, Helvetica, sans-serif;
    text-align:center;
    vertical-align:middle;
    font-weight:bold;
`
const NQ = styled.button`
    width:200px;
    height: 30px;
    font-size:20px;
    border:none;
    background-color:orange;
    color:white;
    position:absolute;
    visibility: hidden;
    left: 50px;
    top: 50px;
`


class Game extends Component {
    state = {
        currentQuestion: 0,
        answerPossibilities: ["5+2", "5+1", "5+1", "5+1"],
        questions: questionJson.questions,
        currentAnswer: null,
        color: [white, white, white, white],
        evalutationActive: false,
        questionText: null,
        gameOver: true,
        endGameText: ""
    }

    initGame = () => {
        var answersText = []
        for (var i = 0; i < 4; i++) {
            answersText[i] = this.state.questions[0].possibilities[i]
        }
        this.setState({ color: [white, white, white, white] })
        this.setState({ questionText: questionJson.questions[0].question })
        this.setState({ currentQuestion: 0 })
        this.setState({ currentAnswer: questionJson.questions[0].anwser })
        this.setState({ answerPossibilities: questionJson.questions[0].possibilities })
        // document.getElementById("nq").style.visibility = "none"
    }

    resetGame = () => {
        this.setState({ gameOver: false })
        this.initGame()
    }

    nextQuestion = () => {
        //disable button 
        console.log("nq")
        document.getElementById("nq").style.visibility = "hidden"
        this.setState({ currentQuestion: this.state.currentQuestion + 1 }, () => {
            var questionsText = []
            for (var i = 0; i < 4; i++) {
                questionsText[i] = this.state.questions[this.state.currentQuestion].possibilities[i]
            }
            this.setState({ currentAnswer: questionJson.questions[this.state.currentQuestion].anwser })
            this.setState({ questionText: questionJson.questions[this.state.currentQuestion].question })
            this.setState({ answerPossibilities: questionJson.questions[this.state.currentQuestion].possibilities })
        })

        // set all buttons to white
        this.setState({ color: [white,white,white,white]})
    }

    evaluateQuestion = (e) => {
        if (!this.state.evalutationActive) {
            const answerPlayer = e.target.id
            var correct = false
            var color = Array.from(this.state.color)
            var index = null

            // change button to orange
            switch (e.target.id) {
                case 'A':
                    index = 0
                    break;
                case 'B':
                    index = 1
                    break;
                case 'C':
                    index = 2 
                    break;
                case 'D':
                    index = 3
                    break;
                default:
            }

            color[index] = orange
            this.setState({ color: color }, () => {})
            
            if (answerPlayer === this.state.currentAnswer) {
                correct = true
            }
            
            

            if (correct === true) {
                // change button to green
                setTimeout(() => {
                    color[index] = green
                    this.setState({color: color})
                    document.getElementById("nq").style.visibility = "visible"
                }, 7000)
                
                if(this.state.currentQuestion===14){
                    this.setState({ gameOver: true })
                    this.setState({ endGameText: "Gewonnen" })
                }
            } else {
                //game end screen
                setTimeout(() => {
                    this.setState({ gameOver: true })
                    this.setState({ endGameText: "Verloren" })
                }, 7000)
            }
        }
    }

    render() {
        return (
            
            !this.state.gameOver ?
            <Wrapper>
                <Ladder currentQuestion={this.state.currentQuestion} />
                <QuestionWrapper>{ this.state.questionText }</QuestionWrapper>
                <Answers>
                    <QuestionButton onClick={(e) => { this.evaluateQuestion(e) }} text={this.state.answerPossibilities[0]} id='A' color={this.state.color[0]} />
                    <QuestionButton onClick={(e) => { this.evaluateQuestion(e) }} text={this.state.answerPossibilities[1]} id='B' color={this.state.color[1]} />
                    <br/>
                    <QuestionButton onClick={(e) => { this.evaluateQuestion(e) }} text={this.state.answerPossibilities[2]} id='C' color={this.state.color[2]} />
                    <QuestionButton onClick={(e) => { this.evaluateQuestion(e) }} text={this.state.answerPossibilities[3]} id='D' color={this.state.color[3]} />
                </Answers>
                <NQ id="nq" onClick={() => { this.nextQuestion() }}>NÃ¤chste Frage</NQ>
            </Wrapper>:
            <Wrapper>
            <EndGameScreen resetGame={this.resetGame} text={this.state.endGameText} />
            </Wrapper>
        )
    }
}

export default Game