import React, { Component } from 'react'
import styled from 'styled-components'

const EndFont = styled.div`
    color:white;
    font-size: 40px;
`
const Wrapper = styled.div`
    position:absolute;
    height:200px;
    width:400px;
    left:270px;
    top:400px;
    background-color: #3848CB;
    text-align:center;
    vertical-align:middle;
    line-height:200px;
`

class EndGameScreen extends Component {
    render() {
        return (
            <Wrapper>
                <EndFont>{this.props.text}</EndFont>
                <button onClick={ this.props.resetGame }>Neues Spiel</button>
            </Wrapper>
        )
    }
}

export default EndGameScreen